$bgBackground: rgba(0,0,0,0.5);
$bgModal: #fff;
$bgHeader: #AFE9C9;
$colorTitle: #fff;

.wrapper {
    position: relative;
    &__background {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 9;
        background: $bgBackground;
    }
}
.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    overflow: auto;
    transform: translate(-50%, -50%);
    z-index: 99;
    background: $bgModal;
    min-width: 600px;
    border-radius: 40px;
    &__header {
        background: $bgHeader;
        padding: 24px;
        position: relative;
        &__cross {
            position: absolute;
            top: 15px;
            right: 15px;
            cursor: pointer;
        }
        &__title {
            color: $colorTitle;
            text-align: center;
            font-weight: 700;
        }
    }
}