.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.navigate{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 125px;
  width: 100%;
}
.navigate__link{
  color: #F364AB;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  font-size: 26px;
  line-height: 32px;
  transition: 0.3s all linear;
  cursor: pointer;
}

.nav__active{
  text-decoration: underline;
  font-weight: 700;
}

.navigate__link:hover{
    transform: scale(1.05);
    opacity: 0.8;
}


.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo img{
  height: 114px;
  width: 75px;
}

.activeStyle{
  border: 1px solid #FFE4F0;
}

.body__crm{
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  gap: 17px;
}

.body__table{
  width: 100%;
  height: 500px;
  border: 2px solid palevioletred;
}