.navigate__left{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &__item{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        height: 84px;
        width: 84px;
        border-radius: 50%;
        transition: 0.3s all linear;
        cursor: pointer;
        opacity: 0.9;
        &:hover{
            transform: scale(1.1);
        }
        &__text{
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 15px;
            /* identical to box height */
            text-align: center;
            color: #F392BF;
        }
    }
}

.navigate__left__item .activelink{
    border: 1px solid #FFE4F0;
    opacity: 1;
}