$transition: 0.1s all linear;
$color: #007433;
.paggination {
    display: flex;
    justify-content: center;
    padding: 18px;
    &__item {
        width: 35px;
        height: 35px;
        background: linear-gradient(90deg, #E1F3E9 0%, #EDF1ED 100%);
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        margin: 0 3px;
        transition: $transition;
        cursor: pointer;
        color: $color;
        font-size: 15px;
        &:hover {
            background: linear-gradient(90deg, #BCEDD2 0%, #B7F1D1 100%);
        }
        &_active {
            background: linear-gradient(90deg, #BCEDD2 0%, #B7F1D1 100%);
        }
    }
}